import React, { useState } from "react";
import { db } from "./backend/firebase-config"; // Import Firestore from your firebase.js file
import { collection, getDocs, query, where, orderBy } from "firebase/firestore";
import "./filter.css";

function Filter() {
  const [vacancies, setVacancies] = useState([]);
  const [keyword, setKeyword] = useState("");
  const [minSalary, setMinSalary] = useState("");
  const [city, setCity] = useState("");
  const [sortByDate, setSortByDate] = useState(false);

  // Helper to parse Firestore timestamps
  const parseTimestamp = (timestamp) => {
    if (!timestamp) return " ";
    const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
    return date.toLocaleDateString();
  };

  // Fetch vacancies from Firebase with filtering and sorting
  const searchVacancies = async () => {
    try {
      let vacancyQuery = collection(db, "vacations");

      if (keyword) {
        vacancyQuery = query(
          vacancyQuery,
          where("About", ">=", keyword),
          where("About", "<=", keyword + "\uf8ff")
        );
      }

      if (minSalary) {
        vacancyQuery = query(vacancyQuery, where("Salary", ">=", Number(minSalary)));
      }

      if (city) {
        vacancyQuery = query(vacancyQuery, where("City", "==", city));
      }

      if (sortByDate) {
        vacancyQuery = query(vacancyQuery, orderBy("add_date", "desc")); // Sort by most recent
      }

      const querySnapshot = await getDocs(vacancyQuery);
      const filteredVacancies = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));

      setVacancies(filteredVacancies);
    } catch (error) {
      console.error("Error fetching vacancies from Firestore:", error);
      setVacancies([]);
    }
  };

  return (
    <div>
      {/* Search Form */}
      <div className="search-box">
        <input
          type="text"
          placeholder=" "
          value={keyword}
          onChange={(e) => setKeyword(e.target.value)}
        />
        <input
          type="number"
          placeholder="  "
          value={minSalary}
          onChange={(e) => setMinSalary(e.target.value)}
        />
        <select value={city} onChange={(e) => setCity(e.target.value)}>
          <option value=""> </option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <label>
          <input
            type="checkbox"
            checked={sortByDate}
            onChange={() => setSortByDate(!sortByDate)}
          />
             (   )
        </label>
      </div>

      {/* Search Button */}
      <button onClick={searchVacancies}></button>

      {/* Results */}
      <div className="vacancy-results">
        {vacancies.length === 0 ? (
          <p>  .</p>
        ) : (
          vacancies.map((vacancy) => (
            <div key={vacancy.id} className="vacancy-card">
              <h3>{vacancy.City || "  "}</h3>
              <p className="salary">${vacancy.Salary || "  "}</p>
              <p className="description">{vacancy.About || " "}</p>
              <p className="date">: {parseTimestamp(vacancy.add_date)}</p>
              <button
                onClick={(e) => {
                  e.target.textContent = " ";
                  e.target.style.backgroundColor = "green";
                  e.target.disabled = true;
                }}
                style={{ backgroundColor: "#007bff", color: "white" }}
              >
                 
              </button>
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default Filter;
